body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-tap-highlight-color: transparent;

  font-family: 'Asap', sans-serif;
  font-size: 30px;


  background: #141E30;  
  background: -webkit-linear-gradient(to left bottom, #243B55, #141E30); 
  background: linear-gradient(to left bottom, #243B55, #141E30);  
}
*{font-family: 'Comfortaa', cursive; font-weight: 400; line-height: 1em; user-select: none;}

.text{
  position: relative;
  cursor: pointer;

  color: white;
  text-align: center;
  width: 100%;

  margin-bottom: 60px;

  font-size: 2em;
}

.copy{
  display: block;
  position: absolute;

  font-size: 20px;
  line-height: 1em;
  color: white;

  background-color: #1b1b1b;

  border-radius: 20px;
  border: 2px solid black;

  padding: 10px 15px;

  left: 50%;
  top: -3em;

  opacity: 0;

  transform: translate(-50%, -50%);
}

.copy.active{
  animation: copy 1s linear;
}

@keyframes copy {
  30% {opacity: 1;}
  60% {opacity: 1;}

  100% {opacity: 0;}
}

input{
  width: 150px;
  height: 1.5em;

  font-size: 1em;
  text-align: center;

  margin-bottom: 1em;

  border-radius: 10px;
}

input::placeholder{
  font-size: 1em;
  line-height: 1em;
  text-align: center;
}

.app{
  position: relative;
  width: 100vw;
  height: 100vh;

  overflow: hidden;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.btn{
  cursor: pointer;
  margin-top: 40px;
  border-radius: 20px;

  background-color: white;

  padding: 10px 30px;

  font-size: 1em;

  transition: all .2s;
}

.btn:hover{
  background-color: #bebaba;
}

.flag{
  position: absolute;
  top: 0;
  left: -10%;

  width: 120vw;

  border: 25px solid;

  border-top-color: #0057b8;
  border-bottom-color: #ffd700;
}

.flag.SS{
  top: unset;
  bottom: 0;

  border-top-color: #cc0000;
  border-bottom-color: #000000;
}

@media (max-width: 1200px){
  .btn:hover{
    background-color: white;
  }

  .btn:active{
    background-color: #bebaba;
  }
}